.add-toy-container {
    margin: 10px;
    * {
        width: 200px;
        text-align: center;
        transition: 0.25s;
        font-size: 1.3rem;
        &:hover {
            letter-spacing: 1px;
        }
    }
}

.filter-container {
    min-height: 100px;
    margin-bottom: 20px;
    flex-wrap: wrap;
}

.filter-container * {
    margin: 0 10px;
    font-size: 1.3rem;
}

.filter-search-box {
    height: 45px;
    border: 0;
    padding-left: 10px;
    outline: none;
    transition: 0.25s;
    background-color: #f1f2f6;
    border-radius: 4px;
    &:focus {
        box-shadow: 0 0 4px $clr5;
        background-color: $clr6;
        position: absolute;
        z-index: 1;
        width: calc(200px + 20vw);
        transform: translateY(120%);
    }
}

.toys-container {
    flex-wrap: wrap;
    width: 100%;
    height: calc(100vh - 100px);
}

.toy-card {
    border: 1px solid $clr1;
    width: 300px;
    height: minmax(fit-content, 100%);
    margin: 20px;
    padding: 10px;
    color: $clr1;
    border-radius: 4px;
    text-align: center;
    & * {
        margin: 10px;
        color: $clr1;
    }
    h2 {
        text-overflow: ellipsis;
        overflow: hidden;
    }
}

.toy-card img {
    width: 100px;
    margin: auto;
}

.toy-card a {
    color: $clr1;
    border: 1px solid $clr1;
    padding: 5px;
    border-radius: 4px;
}
