.toy-chat-icon {
  position: fixed;
  left: 40px;
  bottom: 40px;
  border: 1px solid $clr5;
  background-color: $clr6;
  color: $clr3;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  z-index: 1;
}

.toy-chat {
  position: fixed;
  color: white;
  background-color: #57606f;
  border-radius: 4px;
  height: 400px;
  width: calc(200px + 20vw);
  max-width: 500px;
  left: 40px;
  bottom: 40px;
  padding: 10px;
  z-index: 1;
}

.toy-chat-header {
  flex: 1;
  & h2 {
    color: $clr6;
  }
  border-bottom: 1px solid white;
}

.toy-chat-header button {
  top: 0;
  right: 0;
  position: absolute;
  border: 0;
  border-top-right-radius: 4px;
  outline: none;
  width: 30px;
  background-color: rgb(255, 79, 79);
  color: white;
}

.toy-chat-main {
  flex: 6;
  overflow-y: scroll;
  background-color: #57606f;
  border: 0;
  & * {
    margin: 5px;
  }
}

.toy-chat-footer {
  flex: 1;
  & * {
    width: 100%;
  }
}

.toy-chat-footer input {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  outline: none;
  margin: 0;
  padding: 0;
  border: 0;
  padding-left: 10px;
  height: 100%;
}

.toy-chat-footer button {
  width: 100px;
  height: 100%;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  outline: none;
  border: 0;
  padding: 0;
}

.user-msg {
  @include chat-msg;
  background-color: $clr3;
  margin-left: auto;
  margin-right: 10px;
  & * {
    color: white;
  }
}

.robot-msg {
  @include chat-msg;
  background-color: $clr5;
  & * {
    color: white;
  }
}
