.edit-container form {
    width: calc(300px + 10vw);
    padding: 10px;
    box-shadow: 0 0 2px $clr1;
    border-radius: 4px;
    margin: 50px auto;
    background-color: $clr5;
    > * {
        margin: 15px;
        min-height: 30px;
        border: 0;
        border-radius: 4px;
        padding: 10px;
        outline: none;
        select {
            border: 0;
            margin: 0 10px;
            outline: none;
        }
    }
    :not(img) {
        background-color: $clr6;
        font-size: 1rem;
    }
    h2 {
        text-align: center;
        background-color: transparent;
        color: $clr6;
        font-size: 2rem;
    }
    img {
        align-self: center;
    }
}

.edit-choose-pic {
    cursor: pointer;
    text-align: center;
    color: $clr3;
}

.edit-pic-container{
    flex-wrap: wrap;
    button {
        border: 0;
    }    
}

.edit-preview-img {
    width: 100%;
    margin-bottom: 10px
    ;
}