* {
    box-sizing: border-box;
}

html {
    font-family: montserrat;
    line-height: 1.5em;
}
::-webkit-scrollbar {
    width: 2px;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
    background-color: darkgrey;
 
}

button {
    cursor: pointer;
    outline: none;
}

body {
    margin: 0;
}

img {
    width: 100%;
}

a {
    text-decoration: none;
    cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: montserrat;
    line-height: 1.2em;
    margin: 0 0 rem(20px);
    color: darken($clr1, 36);
}

h1 {
    font-size: rem(70px);
}

p {
    margin: 0 0 rem(20px);
    max-width: 60ch;
}
